.link {
    padding: 10px 20px;
    cursor: pointer;
    transition: all .2s ease;
    text-decoration: none;
    display: inline-flex;
    font-size: 14px;
    justify-content: center;
    align-items: center;
    line-height: 1;
    border-radius: 5px;
}

.link:hover {
    opacity: .9;
}

